<template>
  <div class="smt">   
    <!-- <template v-for="(item,index) in list" >
        <div class="charts" :id="'charts'+ index" :key="index">
            
        </div>
    </template> -->
	<div class="top">
		<div class="title">
			贴片机稼动率
		</div>
		<div class="content">
		<div class="engine" v-for="(item,index) in list" :key="index">
			<div class="name">{{item.DEVICE_NAME}}</div>
            <div class="rateValue" style="position:absolute;z-index:9999;margin-left:1.4%;margin-top:1.7%;">{{item.rate}}</div>
            <i class="fa fa-battery-empty" v-bind:style="{'color':(item.STATUS == '停止'?'#595959':'#ad8b00')}"></i>           
    	</div>
		</div>
	</div>
	<div class="bottom">
		<div id="myCharts">			
		</div>	
	</div>
  </div>
</template>

<script>
export default {
  name: 'Smt',
  data () {
    return {
		xArray:[],
		yArray:[],
		timeArrry:[],
		list: [],
		intervalId:"",
		WARNINGSUM:[],
		url:""
    }
  },
  methods:{
		getNewData(){
		this.url ='http://' + window.location.host;
		console.log(this.url);
      	this.$axios.get(`${this.url}/dafeng_mes/plan3/SMDOEE.do`)
      	.then(res => {
			console.log(res);
			this.list = res.data.list; 
			this.WARNINGSUM = res.data.WARNINGSUM;
			this.getTypeArray();
			this.getCharts(); 
    	});
		},
		//处理数据
		getTypeArray(){
			this.xArray = [];
			this.yArray = [];
			for(let i=0 ;i<this.WARNINGSUM.length;i++){
				this.xArray.push(this.WARNINGSUM[i].name);
				this.yArray.push(this.WARNINGSUM[i].value);
			}
		},
		//异常柱状图
		getCharts(){
			let myChart = this.$echarts.init(document.getElementById("myCharts"));
			let  option = {
					title : {
						text: '贴片机异常分类',
						x:'center',
						y:10,
						textStyle:{
							color:'#fff',
							fontSize: 25,
						}					
					},
					tooltip : {
						trigger: 'axis'
					},
					grid:{
						x:50,
						y2:120
					},
					// legend: {
					// 	data:['蒸发量','降水量']
					// },
					xAxis : [
						{
							type : 'category',
							axisLine: {
								show: false
							},
							axisLabel: {
								show: true,
								interval:0,
								rotate:40,
								textStyle: {
								color: '#fff',  //更改坐标轴文字颜色	    	                   
								}
							},
							axisTick:{
								show: false		   //显示/隐藏刻度
							},
							data : ['无法拾起元件','喂料器索引错误','没有元件','吸附重试次数超过','Clamp','宽度低于设定值','错误的脚数','其它异常时间','停机时间']
						}
					],
					yAxis : [
						{
							type : 'value',
							axisLine: {
								show: false
							},
							axisLabel: {
								show: true,
								textStyle: {
								color: '#fff',  //更改坐标轴文字颜色	    	                   
								}
							},
							axisTick:{
								show: false		   //显示/隐藏刻度
							}
						}
					],
					series : [
						{
							name:'蒸发量',
							type:'bar',
							barWidth : 30,
							itemStyle: {   
								//通常情况下：
								normal:{  
									color:'#f5222d',
			/*		　　　　　　　　　　　//每个柱子的颜色即为colorList数组里的每一项，如果柱子数目多于colorList的长度，则柱子颜色循环使用该数组
									color: function (params){
										var colorList = ['#ff7875','#ff9c6e','#ffc069','#d3f261','#5cdbd3','#69c0ff','#b37feb','#ffd666'];
										return colorList[params.dataIndex];
									},*/
									label: {
										show: true,
										position: 'top',
										textStyle: {
											color: '#fff'
										}
									}				
								}
							},
							data:[2.0, 4.9, 7.0, 23.2, 25.6, 76.7, 135.6, 162.2, 32.6],
							// markPoint : {
							// 	data : [
							// 		{type : 'max', name: '最大值'},
							// 		{type : 'min', name: '最小值'}
							// 	]
							// },
							// markLine : {
							// 	data : [
							// 		{type : 'average', name: '平均值'}
							// 	]
							// }
						}
					]
				};
				myChart.setOption(option);                   
		}
  },
  created(){
	  this.getNewData();
      this.intervalId = setInterval(() => {
            this.getNewData()
        },3000)  
  },
   beforeDestroy () {
        clearInterval(this.intervalId)
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.smt{
    width: 100%;
    height: 650px;
}
.top{
	width: 100%;
	height: 40%;
	border: 1px rgba(238, 238, 238, 0.4)  solid;
    border-radius: 5px;
}
.content{
	padding-left: 20px;
}
.title{
	text-align: center;
	color: #fff;
	font-size: 25px;
}
.bottom{
	width: 100%;
	height: 50%;
	border: 1px rgba(238, 238, 238, 0.4)  solid;
    border-radius: 5px;
	margin-top: 5px;
}
.engine{
    width: 80px;
    height: 65px;
    border: 1px rgba(238, 238, 238, 0.4)  solid;
    border-radius: 5px;
    float: left;
    margin: 5px;
	text-align: center;
	padding-top: 5px;
	box-sizing: border-box;
}
.fa{
    font-size: 46px;
}
.name{
	color:#fff;
	text-align: center;
	font-size: small;
}
.rateValue{
	color:#fff;
	font-size: small;
}
#myCharts{
	width: 100%;
	height: 300px;
}
</style>



